export const conclusion = "To put it simply, my semesters at Magnet Forensics were amazing. Magnet provided exactly what I was looking for in my final co-op terms - experience working with knowledgeable, passionate individuals towards a common goal of making a real difference in the world. This made each day an experience to look forward to, and made me love my work. Through completing goals that were set for each semester, I was able to expand my technical knowledge to a point where I am much more confident in my skills as a developer, while honing my soft skills pertaining to both written and verbal communication. I feel that Magnet has provided me with a sense of confidence that will help me catch my stride when I enter the workforce post-graduation, and for that I am extremely grateful. \
My time at Magnet allowed me to gain experience working on entire web-based products, internal tooling infrastructure, and development tools and platforms such as Gitlab, Jenkins,and Gerrit, that made me a more efficient developer. I was able to apply much of the knowledge that I learned in my academic studies in many aspects of my day-to-day tasks, and was provided with an opportunity to expand my soft-skills through pairing with other developers on tickets, reviewing and testing other developers’ code, and coordinating meetings pertaining to tickets I was working on. I look forward to exercising the skills that I have developed during my eight months at Magnet Forensics during my final eight months of academic studies, and for the duration of my career in computing.";

